import { useEffect, useRef, useState } from "react";
import useSound from "use-sound";
import startSound from "../../assets/audio/start.mp3"
import endSound from "../../assets/audio/end.mp3"
import styles from "./Pomodoro.module.css"

export function Pomodoro() {
    const [minView, setMinView] = useState(0);
    const [secView, setSecView] = useState(0);
    const [workMin, setWorkMin] = useState(0);
    const [breakMin, setBreakMin] = useState(0);
    const [lap, setLap] = useState(0);
    const [status, setStatus] = useState("Session" || "Break" || "Pause")
    const [isPaused, setPause] = useState(true)
    const [isReset, setReset] = useState(true)

    const wrapRef = useRef(null)
    const minViewRef = useRef(minView)
    const secViewRef = useRef(secView)
    const isPausedRef = useRef(isPaused)
    const statusRef = useRef(status)
    const lapRef = useRef(lap)
    const isResetRef = useRef(isReset)

    const [playStart] = useSound(startSound)
    const [palyEnd] = useSound(endSound)

    useEffect(() => {
        const interval = setInterval(() => {
            switchWrap()
            if (isPausedRef.current) {
                return clearInterval(interval)
            }
    
            if (secViewRef.current === 0 && minViewRef.current === 0) {
                return switchStatus()
            }
    
            if (secViewRef.current === 0) {
                minViewRef.current--
                setMinView(minViewRef.current)
                secViewRef.current = 59
               return setSecView(secViewRef.current)
            }

            secViewRef.current--
            setSecView(secViewRef.current)
        }, 1000)
    }, [isPaused])

    const switchStatus = () => {
        if (statusRef.current === 'Session') {
            statusRef.current = 'Break'
            setStatus(statusRef.current)
            minViewRef.current = breakMin
            switchWrap()
            palyEnd()
            return setMinView(minViewRef.current)
        }

        if (statusRef.current === 'Break') {
            statusRef.current = 'Session'
            setStatus(statusRef.current)
            lapRef.current++ 
            setLap(lapRef.current)
            minViewRef.current = workMin
            switchWrap()
            playStart()
            return setMinView(minViewRef.current)
        }

    }

    const showTimmer = () => {
        if (minView < 10 && secView < 10) {
            return `0${minView}:0${secView}`
        } else if (minView >= 10 && secView < 10) {
            return `${minView}:0${secView}`
        } else if (minView < 10 && secView >= 10) {
            return `0${minView}:${secView}`
        } else {
            return `${minView}:${secView}`
        }
    }


    const onClickStart = () => {
        isPausedRef.current = false
        isResetRef.current = false
        setPause(isPausedRef.current)
        setReset(isResetRef.current)

        if (secViewRef.current === 0 && minViewRef.current === workMin) {
            playStart()
        }
    }

    const onClickPause = () => {
        isPausedRef.current = true
        setPause(isPausedRef.current)
    }

    const onClickReset = () => {
        minViewRef.current = workMin
        secViewRef.current = 0
        lapRef.current = 0
        statusRef.current = 'Session'
        isResetRef.current = true
        setStatus(statusRef.current)
        setMinView(minViewRef.current)
        setSecView(secViewRef.current)
        setLap(lapRef.current)
        setReset(isResetRef.current)
        switchWrap()
    }

    const onClickPlusWork = () => {
        setWorkMin(workMin + 1)
        minViewRef.current++
        setMinView(minViewRef.current)
    }

    const onClickMinusWork = () => {
        setWorkMin(workMin - 1)
        minViewRef.current--
        setMinView(minViewRef.current)
    }

    const switchWrap = () => {
        if (isResetRef.current) {
            wrapRef.current.className = styles.wrap
        }

        if (!isResetRef.current && statusRef.current === "Session") {
            wrapRef.current.className = styles.wrap_session
        }

        if (!isResetRef.current && statusRef.current === "Break") {
            wrapRef.current.className = styles.wrap_break
        }
    }

    return (
        <div className={styles.wrap} ref={wrapRef}>
            <div className={styles.work_time}>
                <p className={styles.main_status}>{status} Lap: {lap}</p>
                <p className={styles.main_time}>{showTimmer()}</p>
                {isPaused && !isReset ? <p className={styles.status}>Pause</p> : null}
            </div>

           {(!isReset) ? null : 
            <div className={styles.time}>
                <div className={styles.time_container}>
                    <button className={styles.time_button} onMouseDown={onClickPlusWork}>+</button>
                    <div className={styles.time_item}>
                        <p>Session</p>
                        <p>{workMin < 10 ? 0 : null}{workMin} min</p>
                    </div>
                    <button className={styles.time_button} disabled={workMin === 0} onMouseDown={onClickMinusWork}>-</button>
                </div>
                <div className={styles.time_container}>
                    <button className={styles.time_button} onMouseDown={() => setBreakMin(breakMin + 1)}>+</button>
                    <div className={styles.time_item}>
                        <p>Break</p>
                        <p>{breakMin < 10 ? 0 : null}{breakMin} min</p>
                    </div>
                    <button className={styles.time_button} disabled={breakMin === 0} onMouseDown={() => setBreakMin(breakMin - 1)}>-</button>
                </div>
            </div>
            }

            <div>
                <button className={status === 'Break' ? styles.control_button_break : styles.control_button} 
                disabled={workMin === 0 || breakMin === 0 || !isPaused } 
                onClick={onClickStart}>
                    Start
                </button>
                <button className={status === 'Break' ? styles.control_button_break : styles.control_button}
                disabled={isPaused} 
                onClick={onClickPause}>
                    Pause
                </button>
                <button className={status === 'Break' ? styles.control_button_break : styles.control_button}
                disabled={!isPaused} onClick={onClickReset}>
                    Reset
                </button>
            </div>
        </div>
    )
}